'use client';

import { useState } from 'react';
import { createContent } from '@/services/content.service';

interface FormData {
  type: 'movie' | 'series' | 'program' | 'play' | 'kids' | 'podcast' | 'competition';
  title: string;
  description: string;
  year?: number;
  rating?: number;
  is_premium: boolean;
  category_ids: number[];
}

export default function CreateContentForm({ onSuccess }: { onSuccess: () => void }) {
  const [form, setForm] = useState<FormData>({
    type: 'movie',
    title: '',
    description: '',
    year: undefined,
    rating: undefined,
    is_premium: false,
    category_ids: [],
  });

  const handleChange = (e: any) => {
    const { name, value, type, checked } = e.target;

    let val: any = value;

    if (name === 'year' || name === 'rating') {
      val = value ? Number(value) : undefined; // ุชุญููู ุงููููุฉ ุฅูู number ุฃู undefined
    }

    if (type === 'checkbox') {
      val = checked;
    }

    setForm({ ...form, [name]: val });
  };

  const submit = async (e: any) => {
    e.preventDefault();

    try {
      await createContent(form);
      onSuccess(); // ุฅุนุงุฏุฉ ุชุญููู ุงููุญุชูู ุฃู ุชุญุฏูุซ ุงููุงุฆูุฉ
      setForm({
        type: 'movie',
        title: '',
        description: '',
        year: undefined,
        rating: undefined,
        is_premium: false,
        category_ids: [],
      });
    } catch (err) {
      console.error('ุฎุทุฃ ุนูุฏ ุฅุถุงูุฉ ุงููุญุชูู:', err);
      alert('ุญุฏุซ ุฎุทุฃ ุฃุซูุงุก ุญูุธ ุงููุญุชูู.');
    }
  };

  return (
    <form onSubmit={submit} className="bg-gray-900 p-6 rounded-lg mb-6 text-right shadow-lg">
      <h3 className="text-xl font-bold mb-4 text-red-600">โ ุฅุถุงูุฉ ูุญุชูู ุฌุฏูุฏ</h3>

      <select
        name="type"
        value={form.type}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      >
        <option value="movie">ูููู</option>
        <option value="series">ูุณูุณู</option>
        <option value="program">ุจุฑูุงูุฌ</option>
        <option value="play">ูุณุฑุญูุฉ</option>
        <option value="kids">ุฃุทูุงู</option>
        <option value="podcast">ุจูุฏูุงุณุช</option>
        <option value="competition">ูุณุงุจูุฉ</option>
      </select>

      <input
        name="title"
        placeholder="ุงูุนููุงู"
        value={form.title}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
        required
      />

      <textarea
        name="description"
        placeholder="ุงููุตู"
        value={form.description}
        onChange={handleChange}
        className="w-full mb-3 p-2 bg-gray-800 rounded text-white"
      />

      <div className="grid grid-cols-2 gap-3">
        <input
          type="number"
          name="year"
          placeholder="ุงูุณูุฉ"
          value={form.year ?? ''}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
        <input
          type="number"
          step="0.1"
          name="rating"
          placeholder="ุงูุชูููู"
          value={form.rating ?? ''}
          onChange={handleChange}
          className="p-2 bg-gray-800 rounded text-white"
        />
      </div>

      <label className="flex items-center gap-2 mt-3">
        <input
          type="checkbox"
          name="is_premium"
          checked={form.is_premium}
          onChange={handleChange}
        />
        ูุญุชูู ูุฏููุน
      </label>

      <button
        type="submit"
        className="mt-4 w-full bg-red-600 py-2 rounded hover:bg-red-700 transition"
      >
        ุญูุธ ุงููุญุชูู
      </button>
    </form>
  );
}
